import { Component } from '@angular/core';
import { MatAutocompleteSelectedEvent } from '@angular/material/autocomplete';
import { MatDialog, MatButtonToggleChange, TransitionCheckState } from '@angular/material';
import { TermindetailsComponent } from './termindetails/termindetails.component';
import { Termin } from '../termine/termin';
import { TermineService } from '../termine/termine.service';
import { ProfilService } from '../profil/profil.service';
import { Tag, TagType } from '../tags/tag';
import { TagService } from '../tags/tag.service';
import { SPORT, ESSEN, AFTERWORK, AGILE, MUSIK, REDEN, TECHNIK } from '../tags/tags.democontent';

@Component({
  templateUrl: './terminfindung.component.html',
  styleUrls: ['./terminfindung.component.css']
})
export class TerminfindungComponent {
  public standorte: string[] = ['Hamburg', 'Altenholz', 'Friedrichsort', 'Halle', 'Magdeburg', 'Bremen', 'Rostock', 'Lüneburg'];
  public gefundeneTermine: Termin[] = [];

  public ausgewaehlteStandorte: string[] = [];
  public gewaehlteTags: Tag[] = [];

  public constructor(
    private tagService: TagService,
    private termineService: TermineService,
    private profilService: ProfilService,
    private dialog: MatDialog) {
      this.gewaehlteTags =  this.profilService.getProfilangaben()
        .tags.map(t => tagService.getTags().find(tag => tag.tag === t)).filter(x => !!x);
      this.suchenTermine();
  }

  public anzeigenTerminDetails(termin: Termin) {
    const terminDetailsComponent = this.dialog.open(TermindetailsComponent,
      { width: '98%', maxWidth: '35em' }).componentInstance;
    terminDetailsComponent.termin = termin;
  }

  /**
   * Formatierung für Ende-Datum eines Termin ermitteln.
   * Ist der Termin nicht mehrtägig, interessiert beim Ende-Datum nur die Uhrzeit.
   * @param termin 
   */
  public getEndeDatumsformat(termin: Termin): string {
    const beginnDatum = `${termin.beginn.getFullYear()}-${termin.beginn.getMonth()}-${termin.beginn.getDate()}`;
    const endeDatum = `${termin.ende.getFullYear()}-${termin.ende.getMonth()}-${termin.ende.getDate()}`;
  
    return beginnDatum == endeDatum ? 'HH:mm' : 'dd.MM.yyyy HH:mm';
  }

  public standortAuswahlChanged(e: MatButtonToggleChange) {
    this.ausgewaehlteStandorte = <string[]>e.value;
    this.suchenTermine();
  }

  public suchenTermine() {
    let termine = this.termineService.getTermine();
    
    if (this.ausgewaehlteStandorte.length) {
      termine = termine.filter(t => this.ausgewaehlteStandorte.includes(t.standort));
    }

    if (this.gewaehlteTags.length) {
      const tags = this.gewaehlteTags.map(x => x.tag);
      termine = termine.filter(t => tags.every(tag => t.tags.concat(t.format).includes(tag)));
    }

    this.gefundeneTermine = termine;
  }

  public abswaehlenTag(tag: Tag) {
    const index = this.gewaehlteTags.indexOf(tag);
    if (index < 0) return;

    this.gewaehlteTags.splice(index, 1);
    this.suchenTermine();
  }

  public kategorieClass(termin: Termin) {
    switch(termin.tags[0]) {
      case SPORT:
        return 'sport';
      case ESSEN:
        return 'essen';
      case REDEN:
        return 'reden';
      case AFTERWORK:
        return 'afterwork';
    }
    return termin.tags[0];
  }
}
