import { Component, Output, EventEmitter, Input } from '@angular/core';
import { Tag } from '../tag';
import { TagService } from '../tag.service';
import { MatAutocompleteSelectedEvent } from '@angular/material/autocomplete';

@Component({
  selector: 'app-tag-auswahl',
  templateUrl: './tag-auswahl.component.html'
})
export class TagAuswahlComponent {
  public tagVorschlaege: Tag[] = [];

  public autoCompleteEingabe: string;

  @Input()
  public gewaehlteTags: Tag[] = [];

  @Output()
  public gewaehlteTagsChange = new EventEmitter<Tag[]>();

  public constructor(private tagService: TagService) {
    this.tagVorschlaege = this.tagService.getTags();
  }

  public getTagVorschlaege(): Tag[] {
    const bereitsGewaehlteTypes = this.gewaehlteTags.map(x => x.type);
    const bereitsGewaehlteTags = this.gewaehlteTags.map(x => x.tag);
    return this.tagVorschlaege.filter(x => !bereitsGewaehlteTypes.includes(x.type) && (x.parentTag == undefined || bereitsGewaehlteTags.includes(x.parentTag)));
  }
  
  public getTagAutocomplete(): Tag[] {
    const eingabe = (this.autoCompleteEingabe && this.autoCompleteEingabe.toString() || '').toLowerCase();
    let vorschlaege = this.tagVorschlaege
      .filter(x => !this.gewaehlteTags.includes(x));

    if (eingabe) {
      vorschlaege = vorschlaege.filter(x => x.tag.toLowerCase().indexOf(eingabe) >= 0);
    }

    return vorschlaege;
  }

  public auswaehlenTag(tag: Tag) {
    this.gewaehlteTags.push(tag);
    this.gewaehlteTagsChange.emit(this.gewaehlteTags);
  }

  public auswaehlenTagAusAutocomplete(e: MatAutocompleteSelectedEvent) {
    this.auswaehlenTag(e.option.value);

    setTimeout(() => this.autoCompleteEingabe = '', 0);
  }
}