import { Subject } from 'rxjs';
import { Profilangaben } from './profilangaben';
import { Injectable } from '@angular/core';
import { LocalStorageService } from '../persistenz/local-storage.service';

@Injectable()
export class ProfilService {
  private readonly localStorageKey = 'ProfilService_Profilangaben';
  private readonly profilChangedSubject = new Subject<Profilangaben>();
  private profil: Profilangaben;

  public profilChanged = this.profilChangedSubject.asObservable();

  public constructor(private localStorageService: LocalStorageService) {
    const gespeichertesProfil = localStorageService.get<Profilangaben>(this.localStorageKey);
    this.profil = gespeichertesProfil || new Profilangaben([]);
  }

  public getProfilangaben(): Profilangaben {
    return this.profil;
  }

  public abonnierenTag(tag: string) {
    this.profil.tags.push(tag);
    this.save();
  }

  public entfernenTag(tag: string) {
    const index = this.profil.tags.indexOf(tag);
    if (index < 0) return;

    this.profil.tags.splice(index, 1);
    this.save();
  }

  public setAbonnierteTags(tags: string[]) {
    this.profil.tags = tags;
    this.save();
  }

  private save(): void {
    this.localStorageService.put(this.localStorageKey, this.profil);
  }
}