import { Injectable } from '@angular/core';
import { Subject } from 'rxjs';
import { Termin } from './termin';
import { DemoContentTermine } from './termine.democontent';
import { LocalStorageService } from '../persistenz/local-storage.service';

@Injectable()
export class TermineService {
  private readonly DokumentLocalStorageKeyPrefix = 'TermineService_Dokument_';

  private readonly termineChangedSubject = new Subject();
  private readonly termine: Termin[] = DemoContentTermine;

  public termineChanged = this.termineChangedSubject.asObservable();

  public constructor(private localStorage: LocalStorageService) { }

  public getTermine(): Termin[] {
    return this.termine;
  }

  public getTerminDokumente(termin: Termin): string[] {
    const prefix = `${this.DokumentLocalStorageKeyPrefix}_${termin.titel}_`;

    return this.localStorage.getKeys()
      .filter(k => k.indexOf(prefix) === 0)
      .map(x => x.substring(prefix.length));
  }

  public loadDokument(termin: Termin, name: string): string {
    const prefix = `${this.DokumentLocalStorageKeyPrefix}_${termin.titel}_`;
    return this.localStorage.get(prefix + name);
  }

  public saveDokument(termin: Termin, name: string, dataUrl: string) {
    const prefix = `${this.DokumentLocalStorageKeyPrefix}_${termin.titel}_`;
    this.localStorage.put(prefix + name, dataUrl);
  }
}