import { Component } from '@angular/core';
import { Tag } from '../tags/tag';
import { ProfilService } from './profil.service';
import { TagService } from '../tags/tag.service';

@Component({
  templateUrl: './profil.component.html',
  styleUrls: ['./profil.component.css']
})
export class ProfilComponent {

  public gewaehlteTags: Tag[] = [];

  public benachrichtigungen = ['Agile', 'Fußball', 'Hackathon'];

  public constructor(private tagService: TagService, private profilService: ProfilService) {
    this.gewaehlteTags =  this.profilService.getProfilangaben()
      .tags.map(t => tagService.getTags().find(tag => tag.tag === t)).filter(x => !!x);
  }

  public entfernenTag(tag: Tag) {
    const index = this.gewaehlteTags.indexOf(tag);
    if (index < 0) return;

    this.gewaehlteTags.splice(index, 1);
    this.onTagAuswahlChanged();
  }

  public onTagAuswahlChanged() {
    this.profilService.setAbonnierteTags(this.gewaehlteTags.map(x => x.tag));
  }
}