import { Component } from '@angular/core';
import { Termin } from '../../termine/termin';
import { ProfilService } from 'src/app/profil/profil.service';
import { MatSnackBar } from '@angular/material';

@Component({
  templateUrl: './termindetails.component.html',
  styleUrls: ['./termindetails.component.css']
})
export class TermindetailsComponent {
  public termin: Termin;
  public istAngemeldet = false;
  public anmeldungIsLoading = false;

  public constructor(
    private profilService: ProfilService,
    private snackbar: MatSnackBar) { }

  public istTagAbonniert(tag: string) {
    return this.profilService.getProfilangaben().tags.includes(tag);
  }

  public toggleTagAbo(tag: string) {
    let istAbonniert = this.istTagAbonniert(tag);
    const toggle = () => {
      if(istAbonniert) {
        this.profilService.entfernenTag(tag);
      }
      else {
        this.profilService.abonnierenTag(tag);
      }

      istAbonniert = !istAbonniert;
    }

    toggle();
    let message = !istAbonniert 
      ? `Das Abo für das Tag ${tag} wurde beendet.` 
      : `Das Tag ${tag} wurde abonniert.`;

    const snackbar = this.snackbar.open(message, 'Rückgängig', {
      duration: 5000
    });
    snackbar.onAction().subscribe(() => {
      toggle();
    });
  }

  public getTerminBeschreibungZeilen() {
    return this.termin.beschreibung.split('\n');
  }

  public anmelden() {
    this.istAngemeldet = true;
  }

  public abmelden() {
    this.istAngemeldet = false;
  }

  private toggleAnmeldung() {
    this.istAngemeldet = !this.istAngemeldet;
    this.anmeldungIsLoading = true;

    setTimeout(() => {
      this.anmeldungIsLoading = false;
    }, 1000);
  }
}