import { BrowserModule } from '@angular/platform-browser';
import {BrowserAnimationsModule} from '@angular/platform-browser/animations';
import { NgModule } from '@angular/core';
import {MatButtonModule, MatCheckboxModule, MatIconModule, MatToolbarModule, MatListModule, MatCardModule, MatInputModule, MatChipsModule, MatAutocompleteModule, MatDialogModule, MatTooltipModule, MatButtonToggleModule, MatSnackBarModule, MatTabsModule, MatProgressSpinnerModule} from '@angular/material';
import { RouterModule, Routes } from '@angular/router';
import { FileDropModule } from 'ngx-file-drop';

import { AppComponent } from './app.component';
import { TerminfindungComponent } from './terminfindung/terminfindung.component';
import { TerminplanungComponent } from './terminplanung/terminplanung.component';
import { FormsModule } from '@angular/forms';
import { ProfilComponent } from './profil/profil.component';
import { TermindetailsComponent } from './terminfindung/termindetails/termindetails.component';
import { TermineService } from './termine/termine.service';
import { ProfilService } from './profil/profil.service';
import { LocalStorageService } from './persistenz/local-storage.service';
import { TagService } from './tags/tag.service';
import { TagAuswahlComponent } from './tags/tag-auswahl/tag-auswahl.component';
import { TerminDokumente } from './terminfindung/termin-dokumente/termin-dokumente.component';
import { TeilnehmerComponent } from './terminfindung/teilnehmer/teilnehmer.component';
import { TerminInfoBoxComponent } from './terminfindung/termin-info-box/termin-info-box.component';

const appRoutes: Routes = [
  { path: 'profil', component: ProfilComponent },
  { path: 'terminfindung', component: TerminfindungComponent },
  { path: 'terminplanung', component: TerminplanungComponent },
  { path: '**', redirectTo: 'terminfindung' }
];

@NgModule({
  declarations: [
    AppComponent,
    ProfilComponent,
    TagAuswahlComponent,
    TermindetailsComponent,
    TerminDokumente,
    TerminfindungComponent,
    TerminInfoBoxComponent,
    TerminplanungComponent,
    TeilnehmerComponent
  ],
  imports: [
    BrowserModule,
    BrowserAnimationsModule,
    FileDropModule,
    FormsModule,
    MatAutocompleteModule,
    MatButtonModule,
    MatButtonToggleModule,
    MatCardModule,
    MatCheckboxModule,
    MatChipsModule,
    MatDialogModule,
    MatIconModule,
    MatInputModule,
    MatListModule,
    MatProgressSpinnerModule,
    MatSnackBarModule,
    MatTabsModule,
    MatToolbarModule,
    MatTooltipModule,
    RouterModule.forRoot(appRoutes, {
      useHash: true
    })
  ],
  providers: [
    LocalStorageService,
    ProfilService,
    TagService,
    TermineService
  ],
  bootstrap: [AppComponent],
  entryComponents: [
    TermindetailsComponent
  ]
})
export class AppModule { }
