import { Component, Input } from '@angular/core';
import { Termin } from 'src/app/termine/termin';

@Component({
  selector: 'app-termin-info-box',
  templateUrl: './termin-info-box.component.html',
  styleUrls: ['./termin-info-box.component.css']
})
export class TerminInfoBoxComponent {
  @Input()
  public termin: Termin;
  
  /**
   * Formatierung für Ende-Datum eines Termin ermitteln.
   * Ist der Termin nicht mehrtägig, interessiert beim Ende-Datum nur die Uhrzeit.
   * @param termin 
   */
  public getEndeDatumsformat(termin: Termin): string {
    const beginnDatum = `${termin.beginn.getFullYear()}-${termin.beginn.getMonth()}-${termin.beginn.getDate()}`;
    const endeDatum = `${termin.ende.getFullYear()}-${termin.ende.getMonth()}-${termin.ende.getDate()}`;
  
    return beginnDatum == endeDatum ? 'HH:mm' : 'dd.MM.yyyy HH:mm';
  }
}