export class Termin {
  public constructor(
    public titel: string,
    public beginn: Date,
    public ende: Date,
    public serie: string,
    public terminreihe: string,
    public beschreibung: string,
    public standort: string,
    public format: string,
    tags: string[],
    public teilnehmer?: string,
    public freieplaetze?: string,
  ) { 
    this.tags = tags || [];
  }

  public tags: string[];
}