import { Input, Component, OnInit } from '@angular/core';
import { Termin } from 'src/app/termine/termin';
import { TermineService } from 'src/app/termine/termine.service';
import { UploadEvent, FileSystemFileEntry } from 'ngx-file-drop';

@Component({
  selector: 'app-termin-dokumente',
  templateUrl: './termin-dokumente.component.html',
  styleUrls: ['./termin-dokumente.component.css']
})
export class TerminDokumente implements OnInit {
  public isLoading = false;

  @Input()
  public termin: Termin;

  public dokumente: string[] = [];

  public constructor(private terminService: TermineService) { }
  
  ngOnInit(): void {
    this.refreshDokumenteListe();
  }

  public refreshDokumenteListe() {
    this.dokumente = this.terminService.getTerminDokumente(this.termin);
  }

  public dropped(event: UploadEvent) {
    this.isLoading = true;
    let timeoutDone = false;
    let uploadDone = false;

    // Künstliches "2 Sekunden laden" für echtes Upload Gefühl
    setTimeout(() => {
      timeoutDone = true;
      checkLoading();
    }, 1500);

    const checkLoading = () => {
      if (timeoutDone && uploadDone) {
        this.isLoading = false;
      }
    };

    event.files.forEach(x => {
      if (x.fileEntry.isFile) {
        const file = x.fileEntry as FileSystemFileEntry;
        file.file(f => {
          const reader = new FileReader();
          reader.onload = (e) => {
            this.terminService.saveDokument(this.termin, 
              file.name, <string>reader.result);
            this.refreshDokumenteListe();
            uploadDone = true;
            checkLoading();
          };

          reader.readAsDataURL(f);
        });
      }
    });
  }

  public download(file: string) {
    const dataUrl = this.terminService.loadDokument(this.termin, file);
    console.log(dataUrl);

    // https://stackoverflow.com/questions/3916191/download-data-url-file
    var a = document.createElement("a");
    a.href = dataUrl;
    a.setAttribute("download", file);
    a.click();
    return false;
  }
}