import { Injectable } from '@angular/core';

@Injectable()
export class LocalStorageService {
  private readonly localStorage = window.localStorage;

  public put(key: string, content: any): void {
    this.localStorage.setItem(key, JSON.stringify(content));
  }

  public get<T>(key: string): T {
    const itemAsString = this.localStorage.getItem(key);
    if (!itemAsString) return undefined;

    return JSON.parse(itemAsString);
  }

  public getKeys(): string[] {
    const keys = [];

    for (let i = 0; i < this.localStorage.length; i++) {
      keys.push(this.localStorage.key(i));
    }

    return keys;
  }
}